function InputFile({
  labelTitle,
  labelStyle = "",
  containerStyle = "",
  value = '',
  updateFormValue,
  updateType,
  border,
  size = "md",
  position = "top",
  hasError = false,
}) {
  const fieldSize = `file-input file-input-${size} file-input-${border || "bordered"} ${value ? "join-item" : ""} ${hasError ? 'border-red-500' : ''}`;
  const joinSize = `join ${position === "left" ? "w-3/4" : "w-full"}`;

  const handleFileChange = (e) => {
    const file = e.target.files?.[0] || null;
    updateFormValue(updateType, file);
  };

  return (
    <div
      className={`form-control w-full ${containerStyle}`}
      style={{
        flexDirection: position === "left" ? "row" : "column",
        alignItems: position === "left" ? "center" : "flex-start",
      }}
    >
      <label
        className={`label ${position === "left" ? "w-1/4" : "w-full"}`}
        style={{ marginRight: position === "left" ? "1rem" : "0" }}
      >
        <span className={`label-text ${hasError ? 'text-red-500' : 'text-base-content'} ${labelStyle}`}>
          {labelTitle}
        </span>
      </label>
      <div className={joinSize}>
        <input
          type="file"
          onChange={handleFileChange}
          className={fieldSize}
        />
        {value?.name && (
          <button
            type="button"
            className="btn btn-sm join-item rounded-r-full"
            onClick={() => window.open(URL.createObjectURL(value), '_blank')}
          >
            Ver
          </button>
        )}
      </div>
    </div>
  );
}

export default InputFile;
