// src/components/Input/TextAreaInput.jsx
function TextAreaInput({
  labelTitle,
  labelStyle = "",
  containerStyle = "",
  value = '',
  placeholder,
  updateFormValue,
  updateType, // ya no se usa directamente aqu칤
  border,
  size,
  labelPosition = "top",
  hasError = false,
}) {
  let borderStyle = border ? `textarea-${border}` : "textarea-bordered";
  let sizeStyle = size ? `textarea-${size}` : "textarea-md";
  return (
    <div
      className={`form-control ${containerStyle}`}
      style={{
        display: "flex",
        flexDirection: labelPosition === "left" ? "row" : "column",
        alignItems: labelPosition === "left" ? "center" : "flex-start",
      }}
    >
      <label
        className={`label ${labelStyle} ${labelPosition === "left" ? "w-1/4" : "w-full"}`}
        style={{ marginRight: labelPosition === "left" ? "1rem" : "0" }}
      >
        <span className={`label-text ${hasError ? 'text-red-500' : 'text-base-content'}`}>
          {labelTitle}
        </span>
      </label>
      <textarea
        value={value}
        className={`textarea ${borderStyle} ${labelPosition === "left" ? "w-3/4" : "w-full"} ${sizeStyle} ${hasError ? 'border-red-500' : ''}`}
        placeholder={placeholder || ""}
        onChange={(e) => updateFormValue(e.target.value)} // 游녣 Aqu칤 el cambio
      />
    </div>
  );
}

export default TextAreaInput;
