function CheckBox({
  labelTitle,
  labelStyle = "",
  containerStyle = "",
  value = false,
  updateFormValue,
  updateType,
  size = "md",
  labelPosition = "top",
  hasError = false,
}) {
  const toggleValue = () => {
    updateFormValue(updateType, !value);
  };

  const fieldSize = `checkbox checkbox-${size}${labelPosition === 'left' ? '' : ' w-full'}`;

  return (
    <div
      className={`form-control ${containerStyle}`}
      style={{
        display: "flex",
        flexDirection: labelPosition === "left" ? "row" : "column",
        alignItems: labelPosition === "left" ? "center" : "flex-start",
      }}
    >
      <label
        className={`label cursor-pointer ${
          labelPosition === "left" ? "w-1/4" : "w-full"
        }`}
        style={{ marginRight: labelPosition === "left" ? "1rem" : "0" }}
      >
        <span className={`label-text ${hasError ? 'text-red-500' : 'text-base-content'} ${labelStyle}`}>
          {labelTitle}
        </span>
      </label>
      <input
        type="checkbox"
        className={fieldSize}
        checked={!!value}
        onChange={toggleValue}
      />
    </div>
  );
}

export default CheckBox;
