import React from 'react';
import { InformationCircleIcon } from '@heroicons/react/24/outline';

function SelectBox(props) {
  const {
    labelTitle,
    labelDescription,
    value,
    containerStyle = "",
    placeholder,
    labelStyle = "",
    options = [],
    updateType,
    updateFormValue,
    border = "bordered",
    size = "md",
    labelPosition = "top",
    hasError = false
  } = props;

  const fieldSize = `select select-${border} select-${size} ${labelPosition === "left" ? "w-3/4" : "w-full"} ${hasError ? "border-red-500" : ""}`;

  const handleChange = (e) => {
    updateFormValue(updateType, e.target.value);
  };

  return (
    <div
      className={`form-control ${containerStyle}`}
      style={{
        display: "flex",
        flexDirection: labelPosition === "left" ? "row" : "column",
        alignItems: labelPosition === "left" ? "center" : "flex-start",
      }}
    >
      {labelTitle && (
        <label
          className={`label ${labelStyle} ${labelPosition === "left" ? "w-1/4" : "w-full"}`}
          style={{
            marginRight: labelPosition === "left" ? "1rem" : "0",
          }}
        >
          <span className={`label-text ${hasError ? 'text-red-500' : 'text-base-content'}`}>
            {labelTitle}
            {labelDescription && (
              <div className="tooltip tooltip-right" data-tip={labelDescription}>
                <InformationCircleIcon className="w-4 h-4 ml-1" />
              </div>
            )}
          </span>
        </label>
      )}

      <select
        className={fieldSize}
        value={value || ""}
        onChange={handleChange}
      >
        <option disabled value="">
          {placeholder || "Selecciona una opci칩n"}
        </option>
        {options.map((o, i) => (
          <option value={o.value || o.label} key={i}>
            {o.label}
          </option>
        ))}
      </select>
    </div>
  );
}

export default SelectBox;
